













































import Vue from "vue";
import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast,Dialog } from "vant";
import comhead from "../../components/Comhead.vue";

@Component({
  components: { comhead },
  mixins: [mixin]
})
export default class collection extends Vue {
  goodsList = [];
  checkedGoods=[];
  isShow=false;

  toggle(){
    this.isShow = !this.isShow;
  }
  favdelete(){
        if(this.checkedGoods.length==0){
          Toast('没有选商品')
          return
        }
         Dialog.confirm({
      title: "提示",
      message: "确定要取消收藏吗？"
    })
      .then(() => {
        Vue.prototype.$reqFormPost(
        "/fav/delete",
        {
          userId: this.$store.getters[
              Vue.prototype.MutationTreeType.TOKEN_INFO
          ].userId,
          token: this.$store.getters[
              Vue.prototype.MutationTreeType.TOKEN_INFO
          ].token,
          goodsIds: this.checkedGoods,
        },
        res => {
          if (res == null) {
            console.log("网络请求错误！");
            return;
          }
          if (res.data.status != 200) {
            console.log(
              "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
            );
            Toast(res.data.message);
            return;
          }
          this.getProductList();
          }
          );
          
            })
      .catch(() => {
        // on cancel
      });
  }
  allchange(){
    if (this.goodsList.length==this.checkedGoods.length) {
      this.checkedGoods=[];
    }else{
      let result = [];
      this.goodsList.forEach((item, index) => {
        console.log();
        result.push(item.goodsId);
      });
      this.checkedGoods = result;
    }
  }
  getProductList() {
    Vue.prototype.$reqFormPost(
      "/fav/query",
      {
        userId: this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].userId,
        token: this.$store.getters[
          Vue.prototype.MutationTreeType.TOKEN_INFO
        ].token,
        page:0,
        pageSize:20,
      },
      res => {
        if (res == null) {
          console.log("网络请求错误！");
          return;
        }
        if (res.data.status != 200) {
          console.log(
            "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
          );
          Toast(res.data.message);
          return;
        }        
        this.goodsList = res.data.data.goodsList;
        console.log(res.data.data.goodsList)
      }
    );
  }

  goProductDetail(goodsId){
    this.$router.push({
      path: "/productdetail",
      query: {
        goodsId: goodsId
      }
    });
  }


  handlePX(CssName, PxNumber) {
    return CssName +":" +this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /750 * PxNumber +"px;";
  }


  mounted() {
      this.getProductList();
      console.log("收藏页面")
  }

}



